export default {
	props: {
		placeholder: String, //默认提示
		searchKey: String, //模糊搜索的key值
		dataSource: {
			type: Array,
			default: function() { //数据源
				return [];
			}
		}
	},
	data() {
		return {
			list: [],
			name: '',
			backName: ''
		};
	},
	destroyed() {
		clearTimeout(this.t);
	},
	methods: {
		search(e) {
			console.log('e', e)
			let val = e.detail.value;
			let {
				dataSource
			} = this;
			let arr = [];
			for (let i = 0; i < dataSource.length; i++) {
				if (dataSource[i].name.indexOf(val) !== -1) {
					arr.push(dataSource[i]);
				}
			}
			console.log('arr', arr)
			if (!val) {
				this.list = [];
			} else {
				this.list = arr;
			}

		},
		select(item) {
			this.backName = item.name;
			this.list = [];
			this.$emit('select', item);
		},
		hideList() {
			this.list = [];
			this.t = setTimeout(() => {
				this.name = this.backName;
			}, 0);
		}
	}
}
